import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBOnPointerUp } from './pb/OnPointerUp.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1061

/**
 * @internal
 */
export const OnPointerUpSchema: ISchema<PBOnPointerUp> = {
  serialize(value: PBOnPointerUp, builder: ByteBuffer): void {
    const writer = PBOnPointerUp.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBOnPointerUp {
    return PBOnPointerUp.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBOnPointerUp {
    // TODO: this is a hack.
    return PBOnPointerUp.decode(new Uint8Array())
  }
}
