import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBOnPointerDown } from './pb/OnPointerDown.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1060

/**
 * @internal
 */
export const OnPointerDownSchema: ISchema<PBOnPointerDown> = {
  serialize(value: PBOnPointerDown, builder: ByteBuffer): void {
    const writer = PBOnPointerDown.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBOnPointerDown {
    return PBOnPointerDown.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBOnPointerDown {
    // TODO: this is a hack.
    return PBOnPointerDown.decode(new Uint8Array())
  }
}
