import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBGLTFShape } from './pb/GLTFShape.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1041

/**
 * @internal
 */
export const GLTFShapeSchema: ISchema<PBGLTFShape> = {
  serialize(value: PBGLTFShape, builder: ByteBuffer): void {
    const writer = PBGLTFShape.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBGLTFShape {
    return PBGLTFShape.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBGLTFShape {
    // TODO: this is a hack.
    return PBGLTFShape.decode(new Uint8Array())
  }
}
