import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBNFTShape } from './pb/NFTShape.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1040

/**
 * @internal
 */
export const NFTShapeSchema: ISchema<PBNFTShape> = {
  serialize(value: PBNFTShape, builder: ByteBuffer): void {
    const writer = PBNFTShape.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBNFTShape {
    return PBNFTShape.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBNFTShape {
    // TODO: this is a hack.
    return PBNFTShape.decode(new Uint8Array())
  }
}
