import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBPointerEvents } from './pb/PointerEvents.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1062

/**
 * @internal
 */
export const PointerEventsSchema: ISchema<PBPointerEvents> = {
  serialize(value: PBPointerEvents, builder: ByteBuffer): void {
    const writer = PBPointerEvents.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBPointerEvents {
    return PBPointerEvents.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBPointerEvents {
    // TODO: this is a hack.
    return PBPointerEvents.decode(new Uint8Array())
  }
}
