import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBOnPointerDownResult } from './pb/OnPointerDownResult.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1066

/**
 * @internal
 */
export const OnPointerDownResultSchema: ISchema<PBOnPointerDownResult> = {
  serialize(value: PBOnPointerDownResult, builder: ByteBuffer): void {
    const writer = PBOnPointerDownResult.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBOnPointerDownResult {
    return PBOnPointerDownResult.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBOnPointerDownResult {
    // TODO: this is a hack.
    return PBOnPointerDownResult.decode(new Uint8Array())
  }
}
