import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBPlaneShape } from './pb/PlaneShape.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1014

/**
 * @internal
 */
export const PlaneShapeSchema: ISchema<PBPlaneShape> = {
  serialize(value: PBPlaneShape, builder: ByteBuffer): void {
    const writer = PBPlaneShape.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBPlaneShape {
    return PBPlaneShape.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBPlaneShape {
    // TODO: this is a hack.
    return PBPlaneShape.decode(new Uint8Array())
  }
}
