import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBBoxShape } from './pb/BoxShape.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1013

/**
 * @internal
 */
export const BoxShapeSchema: ISchema<PBBoxShape> = {
  serialize(value: PBBoxShape, builder: ByteBuffer): void {
    const writer = PBBoxShape.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBBoxShape {
    return PBBoxShape.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBBoxShape {
    // TODO: this is a hack.
    return PBBoxShape.decode(new Uint8Array())
  }
}
