import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBSphereShape } from './pb/SphereShape.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1015

/**
 * @internal
 */
export const SphereShapeSchema: ISchema<PBSphereShape> = {
  serialize(value: PBSphereShape, builder: ByteBuffer): void {
    const writer = PBSphereShape.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBSphereShape {
    return PBSphereShape.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBSphereShape {
    // TODO: this is a hack.
    return PBSphereShape.decode(new Uint8Array())
  }
}
