import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBOnPointerUpResult } from './pb/OnPointerUpResult.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1065

/**
 * @internal
 */
export const OnPointerUpResultSchema: ISchema<PBOnPointerUpResult> = {
  serialize(value: PBOnPointerUpResult, builder: ByteBuffer): void {
    const writer = PBOnPointerUpResult.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBOnPointerUpResult {
    return PBOnPointerUpResult.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBOnPointerUpResult {
    // TODO: this is a hack.
    return PBOnPointerUpResult.decode(new Uint8Array())
  }
}
