import { ISchema } from '../../schemas/ISchema'
import { ByteBuffer } from '../../serialization/ByteBuffer'
import { PBCylinderShape } from './pb/CylinderShape.gen'

/**
 * @internal
 */
export const COMPONENT_ID = 1016

/**
 * @internal
 */
export const CylinderShapeSchema: ISchema<PBCylinderShape> = {
  serialize(value: PBCylinderShape, builder: ByteBuffer): void {
    const writer = PBCylinderShape.encode(value)
    const buffer = new Uint8Array(writer.finish(), 0, writer.len)
    builder.writeBuffer(buffer, false)
  },
  deserialize(reader: ByteBuffer): PBCylinderShape {
    return PBCylinderShape.decode(reader.buffer(), reader.remainingBytes())
  },
  create(): PBCylinderShape {
    // TODO: this is a hack.
    return PBCylinderShape.decode(new Uint8Array())
  }
}
